import { typeOf } from "./object";
export const delay = (dur = 0) => {
    return new Promise((resolve) => {
        setTimeout(resolve, dur);
    });
};
/**
 * 判断是否为日期对象
 * @author  韦胜健
 * @date    2019/11/5 20:38
 */
export const isDate = (val) => {
    return typeOf(val) === 'date';
};
/**
 * 判断是否为普通对象
 * @author  韦胜健
 * @date    2019/10/15 22:19
 */
export const isPlainObject = (val) => {
    return typeOf(val) === 'object';
};
/**
 * 转义特殊字符
 * @author  韦胜健
 * @date    2019/11/5 20:40
 */
export const encode = (val) => {
    return encodeURIComponent(val)
        .replace(/%40/g, '@')
        .replace(/%3A/ig, ':')
        .replace(/%24/g, '$')
        .replace(/%2C/ig, ',')
        .replace(/%20/g, '+')
        .replace(/%5B/ig, '[')
        .replace(/%5D/ig, ']');
};
/**
 * 节流
 * @author  韦胜健
 * @date    2019/11/16 18:49
 * @param   func                //核心函数
 * @param   wait                //节流时长（该时间段内只执行一次func）
 * @param   options             //额外参数
 * @param   options.trailing    //最后一次应该被触发，默认为true
 * @param   options.leading     //第一次是否立即执行
 */
const throttle = (func, wait, options = {}) => {
    let args, context, previous = 0, timeout;
    let later = () => {
        // console.log('later click：' + Date.now() % 10000)
        previous = options.leading === false ? 0 : Date.now();
        func.apply(context, args);
        args = context = timeout = null;
    };
    return function () {
        args = arguments;
        // @ts-ignore
        context = this;
        let now = Date.now();
        if (!previous && options.leading === false) {
            previous = now;
        }
        let remaining = wait - (now - previous);
        if (remaining <= 0) {
            /*第一次*/
            if (timeout) {
                clearTimeout(timeout);
                timeout = null;
            }
            // console.log('throttle click：' + Date.now() % 10000)
            func.apply(context, args);
            previous = now;
        }
        else if (!timeout && options.trailing !== false) {
            timeout = setTimeout(later, remaining);
        }
    };
};
/**
 * 防抖
 * @author  韦胜健
 * @date    2019/11/16 19:31
 */
const debounce = (func, wait, immediate) => {
    let timeout;
    return function (...args) {
        // @ts-ignore
        const context = this;
        clearTimeout(timeout);
        if (immediate) {
            let callNow = !timeout;
            // @ts-ignore
            if (callNow)
                func.apply(this, args);
        }
        timeout = setTimeout(() => {
            func.apply(context, args);
            timeout = null;
        }, wait);
    };
};
const Utils = {
    delay,
    isDate,
    isPlainObject,
    encode,
    throttle,
    debounce,
};
export default Utils;
